.slideBlock{
    position:relative;
    padding-top: 5px;
}

.slideBlock input[type=range] {
    -webkit-appearance: none;
    width: 100%;
    position: relative;
    background-color: transparent;
  }
  
  
  .slideBlock input[type=range]:focus {
    outline: none;
  }

  .slideBlock .strack {
    width: 100%;
    border-radius: 3px;
    border: 2px solid #000000;
    position: absolute;
    top: 1px;
    height: 24px;
  }
  
  .slideBlock input[type=range]::-webkit-slider-thumb {
    z-index: 99;
    position: relative;
    -webkit-appearance: none;
    border: 2px solid #fff;
    height: 48px;
    width: 12px;
    border-radius: 6px;
    background: #009AFF;
    cursor: pointer;
    margin-left: 2px;
    margin-top: -11px;
    box-shadow: 0 2px 10px 0 rgba(0,0,0,0.5);
    cursor: url(data:image/vnd.microsoft.icon;base64,),all-scroll;
  }

  /* Firefox */
    .slideBlock input[type=range]::-moz-range-thumb {
        z-index: 99;
        position: relative;
        border: 2px solid #fff;
        height: 47px;
        width: 9px;
        border-radius: 6px;
        background: #009AFF;
        cursor: pointer;
        box-shadow: 0 2px 10px 0 rgba(0,0,0,0.5);
        
    }
  
  .slideBlock input[type=range]::-webkit-slider-thumb:active,
  .slideBlock input[type=range]::-webkit-slider-thumb:focus {
    cursor: url(data:image/vnd.microsoft.icon;base64,),all-scroll;
  }
  
  
  .slideBlock input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    cursor: pointer;
    background-color: transparent;
    border-radius: 3px;
    height: 27px;
    -webkit-appearance: none;
    margin-top: -6px
  }

  /* Firefox */
  .slideBlock input[type=range]::-moz-range-track {
    width: 100%;
    cursor: pointer;
    background-color: transparent;
    border-radius: 3px;
    height: 23px;
    -webkit-appearance: none;
  }


  .slideBlock .slideProgress {
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
    box-sizing: content-box;
    background: #B4E0FD;
    position: absolute;
    top: 1px;
    height: 24px;
    border: 2px solid #222222;
    border-right: 0;
  }

  .slideBlock .Price{
    position: absolute;
    top: 4px;
    left: 10px;
    z-index: 9;
  }